
.snake {
  margin: auto;
}
.snake h1, .snake h3 {
    text-align: center;
    margin-bottom: 3px;
}
.snake h3 {
    color: grey;
    margin: 0;
}
.snake .show-action {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.snake .show-action > .show {
    -webkit-box-flex: 1.5;
        -ms-flex: 1.5;
            flex: 1.5;
}
.snake .show-action > .show {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.snake .action {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.content {
  border: 2px solid black;
  position: relative;
}
.content .over-block {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    text-align: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    display: none;
}
.content h3 {
    color: white;
}
.content .over {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
td {
  height: 15px;
  width: 15px;
}
td.body {
  background-image: url(../assets/body.png);
  background-size: 100%;
}
td.food {
  background: #595959;
  border-radius: 50%;
}
td.wall {
  background: black;
}



